body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.button {
  min-width: 180px;
  margin: 0;
  padding: 13px 22px;
  cursor: pointer;
  box-sizing: border-box;
  border: 1px solid #ffffff;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: inherit;
  background-color: transparent;
  vertical-align: middle;
  text-align: center;
  align-self: center;
  text-transform: uppercase;
  white-space: nowrap; }
  .button--menu {
    position: relative;
    padding: 0;
    width: 37px;
    height: 13px;
    min-width: 0;
    border: none; }
    .button--menu::before, .button--menu::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #ffffff; }
    .button--menu::before {
      top: 0px; }
    .button--menu::after {
      bottom: 0px; }

.site-menu {
  margin: 0;
  padding: 0;
  list-style: none; }
  .site-menu__list {
    display: none; }

.main-page-wrapper {
  padding-top: 33px;
  color: #ffffff;
  background-color: #1B2D37;
  background-image: url("../img/bg-header.png");
  background-position: center center; }

@media (max-width: 1023px) {
  .main-page-wrapper {
    padding-top: 20px; } }

.page-header {
  max-width: 1411px;
  width: 73.5%;
  margin: 0 auto;
  display: flex;
  align-items: center; }
  .page-header__logo {
    margin-right: 8.4%;
    min-width: 202px; }
  .page-header__main-nav {
    margin-right: 20px; }
  .page-header__contacts-list {
    margin: 0;
    padding: 0;
    list-style: none;
    width: 30.7%;
    min-width: 360px;
    margin-left: auto;
    margin-right: 3.4%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .page-header__contacts-item--mail {
    min-width: 136px;
    margin-right: 20px; }
  .page-header__contacts-item--phone {
    min-width: 215px; }
  .page-header__contacts-link {
    padding: 13px 0;
    padding-left: 40px;
    position: relative;
    color: #ffffff; }
    .page-header__contacts-link::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain; }
    .page-header__contacts-link--mail::before {
      width: 19px;
      height: 14px;
      background-image: url("../img/icon-mail.svg"); }
    .page-header__contacts-link--phone::before {
