      height: 18px;
      left: 4px;
      background-image: url("../img/icon-phone.svg"); }

@media (max-width: 1023px) {
  .page-header {
    width: 93.3%;
    min-width: 700px; }
    .page-header__main-nav {
      margin-left: auto; }
    .page-header__contacts-list {
      margin-left: 15px;
      margin-right: 0;
