      width: 10%; }
    .page-header__contacts-item {
      min-width: 25px; }
      .page-header__contacts-item--mail {
        margin-right: 25px; }
    .page-header__contacts-link {
      padding-left: 25px; }
      .page-header__contacts-link span {
        display: none; }
      .page-header__contacts-link--mail::before {
        width: 25px;
